/*
 * NotFound scss File
*/

@import '../../flex.scss'; 
$bg: #f1edea;
.not-found {
  .content-wr {
    background-color: $bg;
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

// 404
.error { 
  position: relative;
  margin-top: 10px;
  color: #45413e;
  font-size: 48px;
  font-weight: 900;
  span {
    padding: 0 30px; 
    position: relative;
    background-color: $bg;
  }
  hr {
    position: absolute;
    top: 5px;
    width: 100%
  }
}

.not-found__content {
  @include flex;
  @include flex-jc-space-around;
  background-color: #ffffff;
  border-radius: 6px;
  padding: 50px 30px;
  margin-top: 30px;
  overflow: hidden;
}
.oops {
  width: 400px;
  height: 400px;
  @include flex-v-center;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    z-index: 10;
    left: -104px;
    top: 30px;
    width: 366px;
    height: 366px;
    right: 146px;
    opacity: 0.1;
    background-image: url('/images/exclamation_mark_2.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}
.title_h1 {
  color: #84bc22;
  font-size: 113px;
  font-weight: 900;
  padding: 0 50px;
}
.subtitle_h5 {
  color: #000000;
  font-size: 24px;
  font-weight: 900;
}
.content-block {
  text-align: left;
  a {
    color: #84bc22;
    font-size: 16px;
    font-weight: 400;
  }
  p {
    color: #555555;
    font-size: 16px;
    font-weight: 400;
    max-width: 550px;
    margin-top: 20px;
    &:last-child {
      margin-bottom: 50px;
    }
  }
  .subtitle_h5:first-child {
    margin-bottom: 10px;
  }
}
.go-home {
  display: inline-block;
  margin-top: 50px;
  margin-bottom: 70px;
}
.search-wr {
  @include flex-vh-center;
  height: 36px;
  margin-top: 25px;
  border: 2px solid #e8e2d6;
  border-radius: 40px;
}
.search {
  width: 90%;
  height: 100%;
  padding: 0;
  padding-left: 20px;
  text-transform: uppercase;
  color: #45413e;
  font-size: 14px;
  font-weight: 400;
}

.search-btn {
  width: 40px;
  height: 40px;
  span {
    border-radius: 40px ;
  }
}



/* ______________________ [ MEDIA ] __________________ */

@media (max-width: 960px) {
  .not-found__content {
    @include flex-column;
    @include align-items-center;
  }
  .oops {
    margin-bottom: 30px;
  }
}
@media (max-width: 768px) {
  .not-found__inner {
    padding: 0 15px;
  }
  .not-found__content {
    padding: 20px 30px 30px 30px;
  }
}
@media (max-width: 600px) {
  .go-home {
    margin: 30px 0;
  }
  .not-found__inner {
    padding: 0 5px;
  }
}
@media (max-width: 360px) {
  .oops {
    @include flex-jc-center;
  }
}
@media (max-width: 360px) {
  .oops {
    .title_h1 {
      font-size: 95px;
    }
  }
}