
/*
 * FeaturedProducts scss File
*/

@import '../../flex.scss'; 

$bg: #262a32;
.featured-products {
  height: 555px;
  .content-wr {
    background-color: $bg;
    height: 100%;
  }
}
.featured-products__inner {
  padding: 50px 0;
  margin-bottom: 64px;
}
.featured-products__title {
  margin-bottom: 84px;
  p {
    font-family: "Lato - Regular";
    color: #ffffff;
    font-size: 18px;
    margin-top: 5px;
  }
  h3 {
    font-family: "Lato - Regular";
    font-weight: bold;
    font-size: 36px;
    color: #ffffff;
  }
}

.featured-products__slider {

  .slick-list { border-radius: 6px; }
  .slick-slider {
    &::before {
      content: '';
      position: absolute;
      height: 3px;
      width: 422px;
      background-color: #ffffff;
      left: 378px;
      top: -50px;
    }
    &::after {
      content: '///';
      position: absolute;
      background-color: $bg;
      padding: 2px 11px;
      font-style: italic;
      font-family: 'Lato', sans-serif;
      color: #ffffff;
      font-weight: 900;
      letter-spacing: 2px;
      font-size: 23px;
      left: 564px;
      top: -65px;
    }
  }
  .slick-prev, .slick-next {
    background-repeat: no-repeat;
    background-position: center;
    width: 30px;
    height: 30px;
    top: -50px;
    &::before { display: none; }
  }
  .slick-prev {
    left: 344px;
    background-image: url('/icons/arrow_slider_left.png');
  }
  .slick-next {
    right: 344px;
    background-image: url('/icons/arrow_slider_right.png');
  }
}


/* ______________________ [ MEDIA ] __________________ */

@media (max-width: 1280px) {
  .featured-products__slider {
    .slick-slider {
      &::before { left: 314px; }
      &::after { left: 500px; } 
    }
    .slick-prev, { left: 280px; }
    .slick-next { right: 280px; }
  }
}

@media (max-width: 1200px) {
  .featured-products__slider {
    .slick-slider {
      &::before {
        left: 236px;
        width: 398px;
      }
      &::after {
        left: 414px;
      }
    }
    .slick-prev, {
      left: 200px;
    }
    .slick-next {
      right: 200px;
    }
  }
}

@media (max-width: 1024px) {
  .featured-products__slider {
    .slick-slider {
      &::before {
        width: 409px;
      }
      &::after {
        left: 418px;
      }
    }
  }
}

@media (max-width: 960px) {
  .featured-products__slider {
    .slick-slider {
      &::before {
        left: 145px;
        width: 470px;
      }
      &::after {
        left: 360px;
      }
    }
    .slick-prev, {
      left: 110px;
    }
    .slick-next {
      right: 110px;
    }
  }
}
@media (max-width: 800px) {
  .featured-products {
    display: none;
  }
}
