/*
 * ProductDetailed scss File
*/

@import '../../flex.scss'; 


$bg: #f1edea;
$textColor: #45413e;
$green: #84bc22;


.product {
  .btn-green {
    border: 2px solid #e8e2d6;
  }
}
.section-product-info {
  .content-wr {
    background-color: $bg;
  }
  .hr {
    background-color: #e5e2df;
    height: 2px;
    top: 25px;
    opacity: 1;
  }
  .title_h2 {
    position: relative;
    display: inline-block;
    color: $textColor;
    font-weight: 900;
    padding: 0 70px;
    background-color: $bg;
  }
  .Breadcrumbs {
    margin-bottom: 24px;
    padding-top: 150px;
  }
}
.section-popularItems-product {
  .content-wr {
    background-color: $bg;
  }
}
.popularItems-product__inner {
  padding: 50px 0;
  .title_h3 {
    color: $textColor;
    text-transform: uppercase;
    background-color: $bg;
    padding: 0 30px;
    font-weight: 900;
  }
}



// slider
.popularItems-product__slider {
  margin-top: 25px;
  .slick-dots {
    bottom: -25px;
    display: none !important;
  }
  .slick-dots li button:before {
      color: #262a32;
      height: 10px;
      width: 10px;
      font-size: 10px;
      opacity: 0.5;
  }
  .slick-dots li.slick-active button:before { 
    color: transparent;
    opacity: 1;
    border: 3px solid #262a32;
    border-radius: 100%;
    height: 10px;
    width: 10px;
  }
}

// gallery
.product__ImageGallery {
  .image-gallery-fullscreen-button {
    top: 20px;
    left: 14px;
    height: 20px;
    width: 20px;
    cursor: pointer;
    background-image: url('/icons/fullscreen.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    &::before {
      display: none;
    }
  }
  .image-gallery-thumbnail {
    border: 2px solid #a2a09e;
    margin-top: 10px;
    &:first-child {
      margin-top: 0; //13 px
    }
    &.active {
      border: 2px solid $green;
    }
  }
  .image-gallery-image {
    img {
      height: 525px;
    }
  }
  .image-gallery-content {
    &.fullscreen {
      .image-gallery-image {
        height: 100vh;
        @include flex-vh-center;
        img {
          max-height: 600px;
          max-width: 600px;
        }
      }
    .image-gallery-thumbnails {
        left: -100%;
        top: 20%;
    }
    }
  }
  .image-gallery-thumbnails {
    margin-left: 30px;
    margin-top: 10px;
  }
  .image-gallery-bullets {
    display: none;
    bottom: -150px;
  }
  .image-gallery-bullet {
    border: none;
    background: gray;
    box-shadow: none;
  }
  .image-gallery-bullets .image-gallery-bullet.active {
    background: white;
    border: 2px solid black;
  }
}
#scroll-top {
  display: none;
  margin: 30px auto;
}
.for-print {
  display: none;
}


/* ______________________ [ MEDIA ] __________________ */

@media (max-width: 960px) {
  .product__tabs {
    display: none;
  }
}
@media (max-width: 600px) {
  .section-popularItems-product {
    .content-wr {
      padding-bottom: 30px;
    }
  }
  .popularItems-product__slider {
    .slick-dots { display: block !important; }
  }
  #scroll-top {
    display: block;
  }
}
@media (max-width: 480px) {
  .section-product-info .title_h2 {
    padding: 0px 5px;
  }
  .product {
    .title_h2 {
      font-size: 26px;
    }
  }
}
