/*
 * Login scss File
*/

@import '../../flex.scss'; 
$loginModalWidth: 500px;
$loginModalHight: 467px;

.login-wr {
  @include flex-vh-center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 11;
	&.active {
    opacity: 1;
    visibility: visible;
    .login__modal {
        transform: none;
    }
  }
}
.login__modal {	
  width: $loginModalWidth;
  height: $loginModalHight;
  box-sizing: border-box;
  position: relative;
  z-index: 999;
  background-color: #ffffff;
  transition: all 0.3s ease;
  transform: scale(1.2);
  padding: 24px;
  border-radius: 6px;
  text-align: left;
}
.login__close {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 10px;
  right: 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url('/icons/close.png');
  cursor: pointer;
}
.login__overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #000000;
  z-index: 15;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  &.active {
    opacity: 0.25;
    visibility: visible;
  }
}
.login__inner {
  height: 100%;
  @include flex-vh-center; 
  @include flex-column;
}
.login-title {
  color: #45413e;
  font-size: 30px;
  font-weight: 900;
}
.login-link {
  margin-top: 24px;
  text-align: center;
  a {
    color: #84bc22;
    font-size: 12px;
    letter-spacing: 0.6px;
  }
}
.login-btn-wr {
  text-align: center;
  margin-top: 7px;
}
.login-inputs {
  width: 400px;
  margin-top: 53px;
}
.login__input-wr {
  position: relative;
  @include flex-vh-center;
  height: 40px;
  margin-top: 20px;
  padding: 2px 2px;
  box-sizing: border-box;
  border: 2px solid #e8e2d6;
  border-radius: 40px;
  margin-bottom: 50px;
  label {
    position: absolute;
    top: -25px;
    left: 0;
    color: #45413e;
  }
  input {
    outline: none;
    &:focus {
      outline: none;
    }
  }
}
.password, .email {
  width: 90%;
  height: 100%;
  padding: 0;
  color: #45413e;
  font-size: 14px;
  font-weight: 400;
  border: none;
  background: transparent;
}
input:-internal-autofill-selected {
  -webkit-box-shadow: inset 0 0 0 50px #fff;
}
.password {
  padding-left: 20px;
  padding-right: 10px;
}
.email {
  padding: 0 10px;
}
.forgot-passw {
  position: absolute;
  top: -25px;
  right: 0;
  color: #84bc22;
  font-size: 12px;
}


/* ______________________ [ MEDIA ] __________________ */
@media (max-width: 600px) {
  .login__modal {	
    width: 430px;
    height: 450px;
  }
  .login-inputs {
    width: 350px;
  }
}
@media (max-width: 480px) {
  .login__modal {	
    width: 350px;
    height: 400px;
  }
  .login-inputs {
    width: 300px;
  }
}
@media (max-width: 360px) {
  .login__modal {	
    width: 300px;
    height: 380px;
  }
  .login-inputs {
    width: 260px;
  }
}
