/*
 * Print scss File
*/

@import '../../flex.scss'; 

.section-print {
  .title_h2 {
    color: #000;
    font-weight: 900;
    br {
      display: none;
    }
  }
}
.print__inner {
  padding-top: 100px;
  .btn_vs_input, 
  .color-select-wr,
  .showTextBtn-wr,
  .print-wr,
  .social-wr,
  .image-gallery-fullscreen-button {
    display: none;
  }
  .product__ImageGallery .image-gallery-thumbnail.active {
    border: 2px solid #000;
  }
  .Breadcrumbs {
    margin-bottom: 24px;
  }
  .link-NotActive {
    &.gray {
      color: #bdc3c7;
    }
  }
  .for-print {
    display: block;
    p {
      color: #000000;
      font-size: 14px;
      font-weight: 900;
      &:first-child {
        margin-top: 53px;
      }
      &:last-child {
        margin-top: 48px;
        margin-bottom: 27px;
      }
    }
  }
}

/* ______________________ [ MEDIA ] __________________ */

@media (max-width: 960px) {
  .section-print {
    .title_h2 { br {display: block;} }
  }
}
