/*
 * Category scss File
*/

@import '../../flex.scss'; 


$bg: #f1edea;
$textColor: #45413e;
$green: #84bc22;


.section-category-services {
  .content-wr {
    background-color: $bg;
  }
  .Breadcrumbs {
    padding-top: 150px;
    margin-bottom: 24px;
  }
  .hr {
    background-color: rgb(255, 255, 255);
    height: 2px;
    top: 25px;
    opacity: 0.5;
  }
  .title_h2 {
    position: relative;
    display: inline-block;
    color: $textColor;
    font-weight: 900;
    padding: 0 70px;
    background-color: $bg;
  }
}
// filter 
.services-filter {
  @include flex-v-center;
  @include flex-space-between;
  background-color: #fff;
  border-radius: 6px;
  padding: 30px 35px;
  margin-top: 50px;
  transition: all 0.5s ease;
  &.active {
    height: 0;
    overflow: hidden;
    padding: 0;
    margin: 0;
  }
}


.selects-col {
  @include flex-column;
  width: 300px;
}
.select-vs-name {
  @include flex-v-center;
  @include flex-space-between;
  text-align: left;
  margin-top: 40px;
  margin-bottom: 20px;
}
.select-name {
  color: rgb(69, 65, 62); 
  font-weight: 600;
  margin-right: 30px;
  white-space: nowrap;
}
.services-option {
  color: rgb(69, 65, 62);
  font-family: 'Lato', sans-serif;
  font-size: 14px;
}

.results {
  font-size: 16px;
  color: rgb(69, 65, 62);
  margin: 30px 0;
}
.services-cards-wr {
  padding-bottom: 80px;
}
.services-cards {
  @include flex;
  @include flex-space-between;
  @include flex-wrap-wr;
  margin-bottom: 120px;
}

// Select
.services-select-wr {
  display: inline-block;
  .select-value {
    padding: 0;
  }
}
.styled-select {
  width: 200px;
  outline: none;
  border: 2px solid rgba(69, 65, 62, 0.7);
  border-radius: 40px;  
  .select-menu {
    border: 2px solid rgba(69, 65, 62, 0.7);
    border-radius: 6px;
  }
  .select-MenuOuter {
    border-radius: 6px;
  }
  .select-option {
    text-align: left;
    padding-left: 30px;
    color: #45413e;
    font-family: 'Lato', sans-serif;
    font-size: 14px;
  }
  .select-control {
    border: none;
    border-radius: 40px; 
    padding-right: 10px;
    padding-left: 30px; 
  }
  .select-placeholder {
    text-align: left;
  }
  .select-MultiValueWrapper {
    outline: none;
    padding: 0;
  }
  --styled-select-placeholder__color: #45413e;
  --styled-select-placeholder__font-family: 'Lato', sans-serif;
  --styled-select-placeholder__font-size: 14px;
  --styled-select-placeholder__padding: 0 30px;
}
.select-arrowZone {
  .select-arrow {
    border: none;
    background-image: url('/icons/arrow_down_black.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
}
.showFilterBtn-wr {
  display: none;
  margin-top: 40px;
  text-align: center;
}
.showFilterBtn {
  background: transparent;
  border: none;
  text-align: left;
  color: $green;
  font-size: 16px;
  outline: none;
  text-align: center;
  &:focus {
    outline: none;
  }
}

[aria-haspopup="true"] {
  .select-arrowZone {
    .select-arrow {
      transform: rotate(180deg);
    }
  }
}
[aria-haspopup="false"] {
  .select-arrowZone {
    .select-arrow {
      transform: rotate(0);
    }
  }
}


//radios 
.radios {
  @include flex-v-center;
}

$gray: rgb(69, 65, 62);
$azure: rgb(67, 192, 207);
$darkGreen: rgb(61, 64, 47);
$greenRadio: rgb(74, 175, 0);
$blue: rgb(49, 91, 189);
$orange: rgb(236, 97, 21);
$red: rgb(220, 4, 29);
$pink: rgb(239, 234, 215);
.radio-color {
	appearance: none;
	width: 14px;
	height: 14px;
	border-radius: 100%;
	cursor: pointer;
  box-sizing: content-box;
	transition: all 0.2s ease;
  background-color: #45413e;
  outline: none;
  border: 3px solid #fff;
  margin: 0 3px;
  &.gray {
    background-color: $gray;
    &:checked { border: 3px solid $gray; background-color: #ffffff; }
  }
  &.azure {
    background-color: $azure;
    &:checked { border: 3px solid $azure; background-color: #ffffff; }
  }
  &.darkGreen {
    background-color: $darkGreen;
    &:checked { border: 3px solid $darkGreen; background-color: #ffffff; }
  }
  &.green {
    background-color: $greenRadio;
    &:checked { border: 3px solid $greenRadio; background-color: #ffffff; }
  }
  &.blue {
    background-color: $blue;
    &:checked { border: 3px solid $blue; background-color: #ffffff; }
  }
  &.orange {
    background-color: $orange;
    &:checked {
      border: 3px solid $orange; background-color: #ffffff; }
  }
  &.red {
    background-color: $red;
    &:checked { border: 3px solid $red; background-color: #ffffff; }
  }
  &.pink {
    background-color: $pink;
    &:checked { border: 3px solid $pink; background-color: #ffffff; }
  }
}


/* ______________________ [ MEDIA ] __________________ */

@media (max-width: 1280px) {
  .services-filter {
    @include flex-wrap-wr;
  }
}
@media (max-width: 1200px) {
}
@media (max-width: 1024px) {
}
@media (max-width: 960px) {
}
@media (max-width: 800px) {
  .services-filter {
    @include flex-column;
    @include align-items-center;
  }
  .select-vs-name {
    margin: 15px 0;
  }
  .showFilterBtn-wr {
    display: block;
  }
  .selects-col {
    &:first-child {
      .select-vs-name {
        &:nth-child(2) {
          margin-top: 20px;
          margin-bottom: 7px;
        }
      }
    }
  }
  .radio-color {
    margin: 0 2px;
  }
}
@media (max-width: 768px) {
  .services-cards {
    @include flex-column;
    @include align-items-center;
  }
}
@media (max-width: 600px) {
  .services-filter {
    padding: 30px 0;
  }
  .selects-col {
    width: 85%;
  }
}
@media (max-width: 480px) {
  .select-name {
    margin-right: 0px;
  }
}
@media (max-width: 320px) {
  .section-category-services {
    .title_h2 {
      font-size: 40px;
      padding: 0px 15px;
    }
    .hr { 
      top: 19px;
    }
  }
}


