@import url('https://fonts.googleapis.com/css?family=Lato:100,100i,300,300i,400,400i,700,700i,900,900i&display=swap');
@import 'flex.scss'; 

@mixin font {
  font-family: 'Lato', sans-serif;
}

$padding: 0 50px;
$green: #84bc22;

html, body {
  margin: 0;
  padding: 0;
  @include font;
}
h1, h2, h3, h4, h5, h6, ul, li, p, a, button {
  list-style: none;
  text-decoration: none;
  color: #24292e;
  @include font;
  font-size: 14px;
  font-weight: 400;
  font-weight: normal;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}
.content-wr {
  max-width: 1280px;
  width: 1280px;
  margin: 0 auto;
  padding: $padding;
  box-sizing: border-box;
}
.title_vs_hr {
  position: relative;
  hr {
    position: absolute;
    top: 5px;
    width: 100%;
    z-index: 0;
    height: 3px;
    background-color: #ffffff;
    opacity: 0.1;
    border: none;
  }
}
.title_h2 {
  color: #ffffff;
  font-size: 48px;
  font-weight: 400;
}
.title_h3 {
  position: relative;
  display: inline-block;
  font-weight: bold;
  font-size: 24px;
}
.subtitle_h6 {
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  margin-bottom: 30px;
  text-align: left;
}

.btn-green {
  font-size: 16px;
  background: $green;
  color: #fff;
  border-radius: 36px;
  width: 180px;
  border: none;
  text-align: center;
  padding: 9px 0;
  border: 2px solid #e8e2d6;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  &.focus {
    outline: none;
  }
  &:hover {
    border: 2px solid $green;
    background: transparent;
    color: $green;
    box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12);
  }
  &.border {
    background: transparent;
    border: 2px solid $green;
    color: $green;
    &:hover {
      border: 2px solid #e8e2d6;
      background: $green;
      color: #fff;
    }
  }
  &.load {
    position: relative;
    background: transparent;
    &::before {
      position: absolute;
      content: '';
      width: 19px;
      height: 15px;
      background-image: url('/icons/load-arrow.png');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      right: 40px;
      top: 12px;
    }
    &:hover {
      border: 2px solid #e8e2d6;
      background: $green;
      color: #000;
      &::before {
        background-image: url('/icons/load-arrow-black.png');
      }
    }
  }
}

// counter vs + - buttons
.counter-wr {
  @include flex;
  border: 2px solid rgb(232, 226, 214);
  box-sizing: border-box;
  width: 110px;
  height: 40px;
  border-radius: 40px;
}
.counter-input {
  width: 30px;
  height: 100%;
  box-sizing: border-box;
  text-align: center;
  border: none;
  outline: none;
  @include font;
  background: transparent;
  &:focus {
    outline: none;
  }
  font-size: 18px;
  color: rgb(69, 65, 62);
}
.counter-btn {
  height: 100%;
  width: 40px;
  font-size: 18px;
  color: rgb(153, 153, 153);
  font-weight: bold;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  &:first-child {
    padding-left: 10px;
  }
  &:last-child {
    padding-right: 10px;
  }
}


// Breadcrumbs
.Breadcrumbs {
  font-weight: 700;
  @include flex-vh-center;
  @include flex-wrap-wr;
}
.link-active {
  font-size: 13px;
  color: #84bc22;
  white-space: nowrap;
}
.link-slash {
  color: #bdc3c7;
  padding: 0 15px;
}
.link-NotActive {
  color: #45413e;
}


// Loader
.Loader {
  animation: Rotate infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.Loader-wr {
  display: none;
}
.Loader-wr.active {
  display: block;
}

@keyframes Rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* ______________________ [ MEDIA ] __________________ */


@media (max-width: 1280px) {
  .content-wr {
    max-width: 1150px;
  }
}

@media (max-width: 1200px) {
  .content-wr {
    max-width: 970px;
  }
}
@media (max-width: 1024px) {
  .content-wr {
    max-width: 920px;
    padding: 0 20px;
  }
}
@media (max-width: 960px) {
  .content-wr {
    max-width: 800px;
  }
}
@media (max-width: 800px) {
  .content-wr {
    max-width: 700px;
  }
}
@media (max-width: 768px) {
  .content-wr {
    max-width: 100%;
  }
}

