/*
 * ProductInfo scss File
*/

@import '../../flex.scss'; 

$textColor: #45413e;
$green: #84bc22;



.product__info {
  @include flex;
  @include flex-space-between;
  padding: 14px 14px 100px 14px;
  margin-top: 50px;
  background-color: #fff;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}
.product__ImageGallery {
  width: 685px;
}
.product__content {
  width: 420px;
  height: 100%;
  padding: 0 15px;
  box-sizing: border-box;
  text-align: left;
  .social-wr {
    text-align: left;
    @include flex;
  }
}
.price {
  color: $textColor;
  font-size: 48px;
  font-weight: 900;
  text-align: left;
  margin-top: 46px;
}
.content-text {
  text-align: left;
  height: 120px;
  overflow: hidden;
  transition: all 0.5s ease;
  color: $textColor;
  font-size: 16px;
  line-height: 24px;
  &.active {
    height: 262px;
    box-sizing: border-box;
  }
}
.showTextBtn-wr {
  margin-top: 10px;
  text-align: left;
}
.showTextBtn {
  background: transparent;
  border: none;
  text-align: left;
  color: $green;
  font-size: 16px;
  outline: none;
  &:focus {
    outline: none;
  }
}
.print-wr {
  margin: 30px 0;
}
.print {
  width: 24px;
  height: 24px;
  outline: none;
}
.share {
  color: $textColor;
}
.btn_vs_input {
  @include flex;
  margin-bottom: 50px;
  .btn-green {
    width: 152px;
  }
  .counter-wr {
    margin-right: 20px;
  }
}
// color - select
.color-select-wr {
  margin-top: 40px;
  margin-bottom: 24px;
  display: inline-block;
  .color-select {
    width: 156px;
    outline: none;
    border: 2px solid #e8e2d6;
    border-radius: 40px;  
    .color-select-menu {
      border: 2px solid #e8e2d6;
      border-radius: 6px;
    }
    .color-select-MenuOuter {
      border-radius: 6px;
    }
    .color-select-option {
      text-align: left;
      padding-left: 40px;
      color: #45413e;
      font-family: 'Lato', sans-serif;
      font-size: 14px;
    }
    .color-select-control {
      border: none;
      border-radius: 40px; 
      padding-left: 40px; 
      padding-right: 10px;
    }
    .color-select-placeholder {
      text-align: left;
      background-image: url('/icons/color-gray.png');
      background-size: 18px;
      background-position: 8%;
      background-repeat: no-repeat;
    }
    .color-select-MultiValueWrapper {
      outline: none;
    }
    --styled-select-placeholder__color: #45413e;
    --styled-select-placeholder__font-family: 'Lato', sans-serif;
    --styled-select-placeholder__font-size: 14px;
    --styled-select-placeholder__padding: 0 10px 0 40px;
  }
  .color-select-arrowZone {
    .color-select-arrow {
      border: none;
      background-image: url('/icons/arrow_down.png');
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      transform: rotate(0);
    }
  }
  .color-select-menu {

    [data-select-option] {
      background-position: 8%;
      background-repeat: no-repeat;
      background-size: 18px;
    }
    [data-select-option="gray"] {
      background-image: url('/icons/color-gray.png');
    }
    [data-select-option="orange"] {
      background-image: url('/icons/color-orange.png');
    }
    [data-select-option="blue"] {
      background-image: url('/icons/color-blue.png');
    }
    [data-select-option="pink"] {
      background-image: url('/icons/color-pink.png');
    }
    [data-select-option="azure"] {
      background-image: url('/icons/color-azure.png');
    }
  }
}

/* ______________________ [ MEDIA ] __________________ */

@media (max-width: 1280px) {
}
@media (max-width: 1280px) {
  .product__ImageGallery {
    .image-gallery-thumbnails {
      margin-left: 0;
    }
  }
  
}
@media (max-width: 1024px) {
  .product__ImageGallery {
    .image-gallery-slide-wrapper {
      min-height: 450px;
      min-width: 450px;
    }
    .image-gallery-content {
      display: flex;
      flex-direction: column;
    }
    .image-gallery-thumbnails-wrapper {
      width: 400px;
    }
    .image-gallery-thumbnails-container {
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
    .image-gallery-thumbnails-wrapper {
      width: 500px;
      margin: 0;
    }
    .image-gallery-thumbnails {
      width: 500px;
      margin: 10px 0 0 0;
      .image-gallery-thumbnail {
        &:last-child {
          display: none;
        }
      }
      .image-gallery-thumbnails-container {
        @include flex-jc-space-around;
        width: 450px;
      }
    }
    .image-gallery-thumbnails-wrapper .image-gallery-thumbnails .image-gallery-thumbnail + .image-gallery-thumbnail, .image-gallery-thumbnails-wrapper.right .image-gallery-thumbnails .image-gallery-thumbnail + .image-gallery-thumbnail {
      margin-left: 0;
    }
    .image-gallery-thumbnail {
      margin-top: 0;
    }
    .image-gallery-bullets {
      display: block;
    }
    .image-gallery-thumbnails-wrapper  {
      height: 104px !important;
    }
  }
}
@media (max-width: 960px) {
  .product__info {
    border-radius: 6px;
    @include flex-column;
    padding: 14px;
  }
  .product__ImageGallery {
    width: 100%;
    .image-gallery-thumbnails {
      left: 60px;
    }
    .image-gallery-content {
      @include align-items-center;
    }
  }
  .product__content {
    margin-top: 50px;
  }
  .social-wr {
    margin: 10px 0;
  }
}
@media (max-width: 768px) {
  .product__info {
    padding-top: 50px;
  }
  .product__ImageGallery {
    width: 500px;
    margin: 0 auto;
    .image-gallery-image img {
      height: 400px;
    }
    .image-gallery-thumbnails {
      .image-gallery-thumbnails-container {
        width: 100%;
      }
    }
    .image-gallery-bullets .image-gallery-bullet {
      margin: 0 5px;
      padding: 5px;
    }
  }
}
@media (max-width: 600px) {
  .btn_vs_input {
    @include flex-column;
    margin-bottom: 40px;
    .btn-green {
      margin-top: 24px;
    }
  }
  .product__ImageGallery {
    width: 100%;
    .image-gallery-slide-wrapper {
      min-height: 100px;
      min-width: 100px;
      width: 100%;
    }
    .image-gallery-image img {
      height: 300px;
    }
    .image-gallery-thumbnails-wrapper {
      width: 100%;
    }
  }
  .product__content {
    width: 100%;
  }
  .product__info {
    padding: 3px;
    padding-bottom: 14px;
  }
}
@media (max-width: 480px) {
  .product__ImageGallery {
    .image-gallery-image img {
      height: 290px;
    }
    .image-gallery-thumbnails-wrapper{
      margin-top: 15px;
    }
  }
}
@media (max-width: 360px) {
  .product__ImageGallery {
    .image-gallery-image img {
      height: 230px;
    }
  }
}
