/*
 * Cookies scss File
*/

@import '../../flex.scss'; 

$textColor: #84bc22;
$modalWidth: 1180px;
.cookies-wr {
  @include flex-vh-center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 11;
	&.active {
    opacity: 1;
    visibility: visible;
    .cookies {
        transform: none;
    }
  }
}
.cookies {	
  width: $modalWidth;
  box-sizing: border-box;
  position: relative;
  z-index: 999;
  background-color: #ffffff;
  transition: all 0.3s ease;
  transform: scale(1.2);
  padding: 24px;
  border-radius: 6px;
  text-align: left;
}
.cookies__close {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 8px;
  right: 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url('/icons/close.png');
  cursor: pointer;
}
.cookies__overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 1);
  z-index: 15;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  &.active {
    opacity: 0.25;
    visibility: visible;
  }
}
.cookies__title {
  color: $textColor;
  font-size: 16px;
  font-weight: 900;
  line-height: 24px;
  margin-bottom: 24px;
}
.cookies__content {
  color: #45413e;
  font-family: Lato;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}
.policy-link {
  color: $textColor;
}
.accept-btn {
  text-align: right;
  .btn-green {
    width: 148px;
  }
}
  
/* ______________________ [ MEDIA ] __________________ */


@media (max-width: 1280px) {
  .cookies {	
    width: 980px;
  }
}
@media (max-width: 960px) {
  .cookies {	
    width: 780px;
  }
}
@media (max-width: 800px) {
  .cookies {	
    width: 700px;
  }
}
@media (max-width: 768px) {
  .cookies {	
    width: 550px;
  }
  .accept-btn {
    margin-top: 15px;
  }
}
@media (max-width: 600px) {
  .cookies {	
    width: 400px;
  }
}
@media (max-width: 480px) {
  .cookies {	
    width: 320px;
  }
}
@media (max-width: 360px) {
  .cookies {	
    width: 300px;
  }
}
