/*
 * Home scss File
*/

@import '../../flex.scss'; 
@import "~slick-carousel/slick/slick.css"; 
@import "~slick-carousel/slick/slick-theme.css";

$sliderWidth: 870px;
$salesWidth: 300px;


.menu-col {
  &.withoutTitle {
    margin-top: 17px;
  }
}
.menu-col__list {
  margin-top: 30px;
  margin-right: 130px;
  >li {
    text-align: left;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: 31px;
  }
}

/* _________________ [ DESCTOP ] ______________________ */

// _____________   [ section - top ] ______________

.top {
  padding-top: 100px;
  margin-bottom: 50px;
}
.top__inner {
  @include flex;
  height: 500px;
}
.slider-wr {
  border-radius: 6px;
  height: 100%;
  width: $sliderWidth;
  background-color: blueviolet;
  margin-right: 30px;
}
.slider-link {
  border: 2px solid #fff;
  border-radius: 40px;
  font-size: 16px;
  color: #fff;
  padding: 8px 15px;
  position: relative;
  z-index: 1;
  &::before {
    display: none;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    border-radius: 40px;
    z-index: -1;
    opacity: 0.2;
  }
  &:hover {
    &::before {
      display: block;
    }
  }
}
.sales-wr {
  @include flex;
  @include flex-jc-end ;
  width: $salesWidth;
}
.sales_inner {
  height: 100%;
  @include flex;
  @include flex-column;
  @include flex-space-between;
}
.sales__card {
  position: relative;
  width: 270px;
  padding: 43px 0;
  background-color: #84bc22;
  border-radius: 6px;
  margin: 0 auto;
  margin-bottom: 44px;
  p {
    color: #ffffff;
    font-size: 24px;
    font-weight: 400;
    text-transform: uppercase;
    &:first-child {
      font-size: 100px;
      margin-bottom: 70px;
    }
  }
}
.sales__img {
  position: absolute;
  bottom: 0;
  left: 0;
}
.sales__link {
  p {
    color: #45413e;
    font-size: 13px;
    font-weight: 400;
    margin: 10px 0;
    &:first-child {
      font-size: 18px;
      font-weight: 400;
      line-height: 18px;
    }
  }
}
.follow {
  margin-top: 20px;
  display: inline-block;
  padding: 6px 33px;
  border: 2px solid #3a5b96;
  text-transform: uppercase;
  color: #3a5b96;
  font-size: 14px;
  font-weight: 400;
  border-radius: 40px;
  .facebook {
    margin-right: 10px;
    vertical-align: bottom;
    fill:#3a5b96;
    g {
      opacity: 1;
    }
  }
  &:hover {
    color: #fff;
    background-color:#3a5b96ed;
    .facebook {
      fill:#fff;
    }
  }
}
.slider-wr {
  .slick-list {
    border-radius: 6px;
  }
  .slick-dots {
    bottom: 50px;
  }
  .slick-dots li button:before {
      color: white;
      height: 10px;
      width: 10px;
      font-size: 10px;
  }
  .slick-dots li.slick-active button:before { 
    color: transparent;
    opacity: 1;
    border: 3px solid white;
    border-radius: 100%;
    height: 10px;
    width: 10px;
  }
}
.slide__inner {
  position: relative;
  width: $sliderWidth;
  height: 500px;
  img {
    width: 100%;
    height: 100%;
  }
}
.slide__content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @include flex;
  @include flex-column;
  padding: 0 60px;
  box-sizing: border-box;
  h4 {
    color: #ffffff;
    font-family: "Myriad Pro";
    font-size: 48px;
    font-weight: 700;
    margin-bottom: 90px;
    margin-top: 70px;
  }
  p {
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 50px;
  }
  &.left {
    @include align-items-start;
    text-align: left;
  }
  &.right {
    @include align-items-end;
    text-align: right;
  }
  &.center {
    @include align-items-center;
    text-align: center;
  }
}

// _____________   [ section - banner ] ______________

.banner {
  height: 445px;
  .content-wr {
    height: 100%;
    background-image: url('/images/banner.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
}
.banner__inner {
  height: 100%;
  @include flex;
  @include flex-column;
  @include flex-vh-center;
  p {
    font-size: 14px;
    font-weight: 400;
    color: #ffffff;
    margin-top: 40px;
  }
}

/* ______________________ [ MEDIA ] __________________ */


@media (max-width: 1350px) {

}
@media (max-width: 1280px) {
  $sliderWidth: 750px;
  $salesWidth: 250px;
  .slider-wr    { width: $sliderWidth; }
  .sales-wr     { width: $salesWidth; }
  .slide__inner { width: $sliderWidth; }
  .sales__card, .sales__inner { width: 100%; }
}
@media (max-width: 1200px) {
  $sliderWidth: 580px;
  $salesWidth: 250px;
  .slider-wr    { width: $sliderWidth; }
  .sales-wr     { width: $salesWidth; }
  .slide__inner { width: $sliderWidth; }
  .slide__content {
    padding: 0 35px;
    h4 {
      margin-bottom: 60px;
      margin-top: 50px;
    }
  }
}
@media (max-width: 960px) {
  $sliderWidth: 500px;
  $salesWidth: 220px;
  .slider-wr    { 
    width: $sliderWidth; 
    .slick-dots {
      bottom: 30px;
    }
  }
  .sales-wr     { width: $salesWidth; }
  .slide__inner { width: $sliderWidth; }
  .slide__content {
    padding: 0 15px;
    h4 {
      margin-bottom: 40px;
    }
  }
}

@media (max-width: 800px) {
  $sliderWidth: 100%;
  .slider-wr    { 
    width: $sliderWidth; 
    .slick-dots {
      bottom: 50px;
    }
  }
  .sales-wr     { display: none; }
  .slide__inner { width: $sliderWidth; }
  .slide__content {
    padding: 0 30px;
    h4 {
      margin-bottom: 70px;
      margin-top: 60px;
    }
  }

}
@media (max-width: 768px) {
  .slide__content h4 {
      margin-bottom: 50px;
      margin-top: 50px;
  }
}
@media (max-width: 600px) {
  .slide__content h4 {
    margin-bottom: 35px;
    margin-top: 20px;
  }
  .slider-wr .slick-dots {
    bottom: 30px;
  }
}
@media (max-width: 480px) {
  .slide__content {
    h4 {
      font-size: 40px;
    }
  }
}
@media (max-width: 360px) {

}
@media (max-width: 320px) {
  .slide__content {
    padding: 0 10px;
  }
  .slide__inner,.top__inner  {
    height: 400px;
  }
  .slide__content h4 {
    font-size: 30px;
  }
  .slide__content p {
    margin-bottom: 30px;
  }
}
