/*
 * Header scss File
*/

@import '../../flex.scss'; 

$headerPadding: 50px;
$textColor: #45413e;
$menuColor: #262a32;
$headerHeight: 80px;
$border: 2px rgb(232, 226, 214);
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  z-index: 10;
  .content-wr {
    padding: 0;
  }
  &.print {
    display: none;
  }
}
.header__inner {
  height: $headerHeight;
  @include flex-v-center;
  @include flex-space-between;
  box-sizing: border-box;
  position: relative;
  background-color: #fff;
  border-bottom: $border solid;
  &.active {
    height: 100%; 
    padding-bottom: 30px; 
    nav {
      height: 100%;
    }
  }
}
.controls {
  padding-right: $headerPadding;
  @include flex-v-center;
  text-align: right;
}
nav {
  width: 100%;
}
.desktop-menu {
  white-space: nowrap;
  height: $headerHeight;
  >li {
    display: inline-block;
    box-sizing: border-box;
    margin-top: 10px;
    height: 71px;
    padding: 19px 12px;
    border: 2px transparent solid;
    >a {
      color: $textColor;
      font-weight: 400;
      text-transform: uppercase;
    }
    &:hover {
      background-color: $menuColor;
      border: $border solid;
      border-bottom: 2px #84bc22 solid;
      a { color: #fff; }
    }
  }
}
.mobile-menu {
  display: none;
  &.active {
    display: block;
    z-index: 2;
    height: 100%;
  }
  p {
    color: #fff;
    font-size: 14px;
    text-transform: uppercase;
  }
  li, a {
    color: #fff;
    text-align: left;
    font-weight: 400;
    text-transform: uppercase;
    padding: 5px 24px;
  }
}
.hamburger {
  display: none;
  width: 48px;
  position: fixed;
  top: 16px;
  left: 100px;
}
.hamburger_icon {
  display: none;
  &.active {
    display: inline-block;
  }
}
.open-drop {
  >a {
    padding-right: 20px;
    position: relative;
    &::after {
      position: absolute;
      content: '';
      top: 6px;
      right: 0;
      height: 6px;
      width: 7px;
      background-image: url('/icons/arrow_down.png');
      background-size: 100% 100%;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  &:hover {
    .drop-menu {
      transform: translate(0);
    }
  }
}
.drop-menu {
  position: absolute;
  left: 0;
  top: $headerHeight;
  width: 100%;
  background-color: $menuColor;
  padding-top: 60px;
  padding-bottom: 30px;
  transition: all 0.3s ease;
  transform: translateY(-150%);
  z-index: -1;
  .content-wr {
    padding: 0 50px;
  }
}
.drop-menu-wr {
  @include flex;
  @include flex-space-between;
}
.menu-col {
  max-width: 160px;
}
.block-menus {
  width: 100%;
  @include flex;
  @include flex-space-between;
}
.block-image {
  >img {
    border-radius: 6px;
    width: 271px;
    height: 276px;
  }
}
.logo-wr {
  padding-left: $headerPadding;
  width: 147px;
  height: 65px;
}
.logo__inner {
  @include flex-vh-center;
}
.logo__text {
  p {
    &:first-child {
      color: #011847;
      font-size: 32px;
      font-weight: 900;
      text-transform: uppercase;
      letter-spacing: 6.33px;
      line-height: 20px;
    }
    &:last-child {
      color: #84bc22;
      font-size: 18px;
      font-weight: 400;
    }
  }
}
.logo {
  width: 63px;
  height: 65px;
  margin-right: 10px;
}
.selects {
  @include flex;
  margin-right: 30px;
  white-space: nowrap;
}
.select-wr {
  margin: 0 20px;
  display: inline-block;
  position: relative;
  &::after {
    position: absolute;
    content: '';
    top: 9px;
    right: 5px;
    height: 6px;
    width: 7px;
    background-image: url('/icons/arrow_down.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
}
select {
  color: $textColor;
  border: none;
  padding: 5px 15px 5px 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  outline: none;
}
.icons-wr {
  @include flex;
  @include flex-space-between;
  @include flex-v-center;
  &.mobile {
    display: none;
    margin: 0 -20px;
    padding: 15px 0;
    background-color: #262a32;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 10;
  }
}
.icons-wr-inner {
  @include flex;
  @include align-items-center;
  @include flex-space-between;
  padding: 0 50px;
}
.icon__inner {
  margin: 0 20px;
  button {
    width: 23px;
    height: 23px;
    background: transparent;
    border: none;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    transform: none;
    transition: all 0.3s ease;
    outline: none;
    &.focus {
      outline: none;
    }
    &:hover {
      transform: scale(1.2)
    }
  }
  .loop {
    background-image: url('/icons/loop.png');
  }
  .loop-footer {
    background-image: url('/icons/loop_footer.png');
  }
  .profile {
    background-image: url('/icons/profile.png');
  }
  .profile-footer {
    background-image: url('/icons/profile_footer.png');
  }
}
.icon {
  padding: 0;
  display: inline-block;
  background: transparent;
  border: none;
  cursor: pointer;
}
.badge {
  cursor: pointer;
}

.icon-cart, .icon-heart {
  transform: none;
  transition: all 0.3s ease;
  &:hover {
    transform: scale(1.2)
  }
}


/* ______________________ [ MEDIA ] __________________ */


@media (max-width: 1200px) {
  .selects {
    margin-right: 0;
  }
  .select-wr {
    margin: 0 5px;
  }
  .icon__inner {
    margin: 0 10px; 
  }
  .block-image {
    img {
      width: 220px;
      height: 255px;
    }
  }
}
@media (max-width: 1024px) {
  .menu-col {
    max-width: 150px;
  }
  .logo-wr {
    padding-left: 20px;
  }
  .controls {
    padding-right: 20px;
  }
  .drop-menu {
    .content-wr {
      padding: 0 30px;
    }
  }
}
@media (max-width: 960px) {
  .header__inner {
    @include flex-column;
  }
  .desktop-menu, .select-wr, 
  .icons-wr, .logo__text,{
    display: none;
  }
  nav, .controls {
    height: 0%;
  }
  .mobile-menu {
    margin-top: 30px;
  }
  .controls {
    padding: 0;
    margin-top: 30px;
  }
  .hamburger {
    display: inline-block;
    position: absolute;
  }
  .header {
    position: absolute;
  }
  .mobile-menu {
    width: 580px;
    margin: 0 auto;
    >.MuiExpansionPanel-rounded:first-child {
      padding-top: 30px;
    }
  }
  .WithStyles\(ForwardRef\(ExpansionPanelSummary\)\)-root-34 {
    min-height: 40px !important;
  }
  .block-image {
    padding: 30px 0;
    background-color: #262a32;
    text-align: center;
  }
  .logo {
    margin-right: 0;
  }
}
@media (max-width: 600px) {
  .mobile-menu {
    width: 450px;
  }
}
@media (max-width: 480px) {
  .mobile-menu {
    width: 350px;
  }
  .block-image > img {
    width: 260px;
  }
  .hamburger {
    left: 20px;
  }
}
@media (max-width: 360px) {
  .mobile-menu {
    width: 300px;
  }

}
