/*
 * Flex scss File
*/

@mixin flex { display: flex; }
@mixin flex-vh-center { display: flex; justify-content: center; align-items: center; }
@mixin flex-v-center { display: flex; align-items: center; }
@mixin flex-column { flex-direction: column; }
@mixin flex-space-between { justify-content: space-between; }
@mixin flex-jc-start { justify-content: flex-start; }
@mixin flex-jc-center { justify-content: center; }
@mixin flex-jc-end { justify-content: flex-end; }
@mixin flex-jc-space-around { justify-content: space-around; }
@mixin flex-row { flex-direction: row; }
@mixin align-items-start { align-items: flex-start; }
@mixin align-items-center { align-items: center; }
@mixin align-items-end { align-items: flex-end; }
@mixin align-self-start { align-self: flex-start; }
@mixin align-self-end { align-self: flex-end; }
@mixin flex-wrap-wr { flex-wrap: wrap; }