/*
 * Cart scss File
*/

@import '../../flex.scss'; 


$bg: #f1edea;
$textColor: #45413e;


.cart {
  .content-wr {
    padding-bottom: 50px;
    padding-top: 100px;
    background-color: $bg;
  }
  .title_vs_hr {

  }
  .hr {
    background-color: #e5e2df;
    opacity: 1;
    top: 36px
  }
  .title_h2 {
    position: relative;
    color: $textColor;
    text-transform: uppercase;
    font-weight: 900;
    display: inline-block;
    padding: 0 25px;
    background-color: $bg;
    margin-top: 16px;
  }
}

.cart__cards-wr {
  background-color: #fff;
  @include flex;
  @include flex-column;
  @include flex-vh-center;
  padding: 100px;
  margin-top: 46px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}
.cart-settings {
  margin-top: 2px;
  background-color: #fff;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
.cart-inner {
  @include flex;
  @include flex-column;
}
.cart__card {
  @include flex;
  @include flex-space-between;
  width: 100%;
  margin-bottom: 30px;
}
.card-block {
  @include flex-v-center;
  @include flex-jc-start;
  &.column {
    @include flex-column;
    @include align-items-start;
  }
}
.card-image {
  width: 96px;
  height: 92px;
  background-color: #c7c7c7;
  img {
    width: 96px;
    height: 92px;
  }
}
.card-info {
  text-align: left;
  margin-left: 21px;
}
.card-title {
  color: $textColor;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  margin-bottom: 12px;
}
.card-price {
  color: #bdc3c7;
  font-weight: 700;
}
.total-price-wr {
  width: 230px;
}
.total-price {
  color: $textColor;
  font-size: 30px;
  font-weight: 900;
  text-transform: uppercase;
}
.delete-wr {
  @include flex-vh-center;
  width: 25px;
  height: 25px;
  border: 2px solid #e8e2d6;
  border-radius: 100%;
}
.delete {
  background: transparent;
  background-image: url('/icons/x.png');
  background-position: center;
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  color: $textColor;
  outline: none;
  border: none;
  border-radius: 100%;
  &.focus {
    outline: none;
  }
  cursor: pointer;
}

// settings
.cart-settings {
  @include flex;
  @include flex-space-between;
  @include align-items-start;
  padding: 100px;
}
.block-inner {
  @include flex;
  &:first-child {
    margin-bottom: 46px;
    width: 100%;
    @include flex-space-between;
  }
  &:nth-child(2) {
    margin-bottom: 40px;
  }
  &:nth-child(3) {
    margin-bottom: 60px;
  }
}
.inner-col {
  width: 220px;
  text-align: left;
}
.setting-name {
  color: $textColor;
  text-transform: uppercase;
  white-space: nowrap;
  &.bold {
    font-weight: 900;
  }
}
.subtotal-price {
  font-weight: 900;
  color: $textColor;
}
.order-price {
  color: $textColor;
  font-size: 48px;
  font-weight: 900;
  text-transform: uppercase;
  white-space: nowrap;
}
.block-buttons {
  @include flex-v-center;
}
#order-col {
  @include flex;
  @include align-items-center;
}
#checkout {
  width: 260px;
}
#update {
  width: 160px;
  margin-right: 16px;
}
#apply {
  width: 111px;
}
#coupon-code {
  width: 216px;
  height: 40px;
  border-radius: 40px;
  box-sizing: border-box;
  border: 2px solid #e8e2d6;
  color: $textColor;
  background: transparent;
  outline: none;
  padding-left: 15px;
  &:focus { outline: none }
  margin-right: 16px;
}

.shipping-radio {
  margin-bottom: 15px;
  >label {
    color: $textColor;
    @include flex-v-center;
    span {
      font-weight: 900;
      margin-left: 4px;
    }
  }
  input {
    appearance: none;
    width: 14px;
    height: 14px;
    cursor: pointer;
    box-sizing: content-box;
    outline: none;
    border: 2px solid #e8e2d6;
    background-color: #ffffff;
    border-radius: 100%;
    transition: all 0.2s ease;
    position: relative;
    margin: 0;
    margin-right: 15px;
    &:checked { 
      &:after {
        display: block;
      }
    }
    &:after {
      position: absolute;
      content: '';
      width: 8px;
      height: 8px;
      background-color: #84bc22;
      top: calc(50% - 4px);
      left: calc(50% - 4px);
      border-radius: 100%;
      display: none;
    }
  }
}
.empty-wr {
  padding: 15px;
}
.empty__message {
  font-family: "Myriad Pro";
  font-size: 30px;
  color: #45413e;
  font-weight: bold;
  margin-top: 20px;
}
.empty__icon {
  width: 100px;
  height: 80px;
}




/* ______________________ [ MEDIA ] __________________ */

@media (max-width: 1380px) {
}
@media (max-width: 1280px) {
  .cart__cards-wr {
    padding: 70px 50px 30px 50px;
  }
  .cart-settings {
    padding: 70px 50px 30px 50px;
  }
}
@media (max-width: 1200px) {
  .cart__cards-wr {
    padding: 70px 30px 30px 30px;
  }
  .cart-settings {
    padding: 70px 30px 30px 30px;
  }
  .total-price-wr {
    width: 165px;
  }
  .card-info {
    padding-right: 30px;
  }
}
@media (max-width: 960px) {
  .cart__card, .cart-settings {
    @include flex-column;
    margin-bottom: 0;
  } 
  .card-block {
    margin: 0 auto;
    margin-bottom: 30px;
    width: 400px;
  }
}
@media (max-width: 800px) {
  .card-block {
    width: 350px;
  }
}
@media (max-width: 600px) {
  #shipping-block {
    @include flex-column;
  }
  .inner-col {
    width: 200px;
  }
  #shipping {
    margin-bottom: 10px;
  }
  #order-col {
    width: 130px;
  }
}
@media (max-width: 490px) {
  .card-block {
    &:first-child {
      @include flex-column;
    }
    @include flex-jc-center;
    width: 100%;
  }
  .total-price-wr {
    width: 100%;
  } 
  .total-price {
    margin: 0 10px;
  } 
  .card-image {
    margin-bottom: 20px;
  }
  .card-info {
    text-align: center;
  }
  .block-inner {
    @include flex-column;
    &:last-child {
      flex-direction: row;
    }
  }
  #block-subtotal {
    flex-direction: row;
  }
  #checkout {
    margin-top: 20px;
  }
  .block-buttons {
    width: 100%;
  }
  #total-block {
    @include flex-column;
  }
  .order-price {
    margin-top: 15px;
  }
  .inner-col {
    width: 100%;
  }
  .subtotal-price {
    margin-left: 15px;
  }
  .cart {
    .title_h2 {
      font-size: 26px;
    }
    .hr {
      top: 24px;
    }
  }
}

@media (max-width: 375px) {
  .total-price {
    margin: 0 5px;
  }
  .cart__card {
    margin-bottom: 15px;
  }
  .block-buttons {
    @include flex-column;
    @include align-items-start;
  }
  .cart-settings {
    .btn-green {
      margin-top: 30px;
    }
  }
  .order-price {
    font-size: 36px;
  }
}
@media (max-width: 320px) {
  #checkout {
    width: 230px;
  }
  .selects-col {
    width: 92%;
  }
  .card-block {
    .counter-wr {
      width: 95px;
    }
  }
  .card-image {
    width: 130px;
    height: 130px;
    img {
      width: 130px;
      height: 130px;
    }
  }
}
