/*
 * Services scss File
*/

@import '../../flex.scss'; 

.services {
  .content-wr {
    background-color: #45413e;
  }
}
.services__inner {
  @include flex;
  @include flex-space-between;
  padding: 50px 0;
}
.services__card {
  @include flex;
}
.services__icon {
  @include flex-vh-center;
  margin-right: 30px;
}
.services__card-content {
  width: 230px;
  text-align: left;
  p {
    color: #868380;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
  }
}


/* ____________________ [ MEDIA ] __________________ */


@media (max-width: 1200px) {
  .services__card-content {
    p {
      padding-right: 20px;
    }
  }
}
@media (max-width: 1024px) {
  .services__icon {
    margin-right: 20px;
  }
}
@media (max-width: 960px) {
  .services__inner {
    @include flex-column;
    @include align-items-center;
  }
  .services__card {
    margin: 30px 0;
    &:nth-child(2n) {
      flex-direction: row-reverse;
      .services__card-content {
        margin-right: 50px;
        margin-left: 0;
      }
    }
  }
  .services__card-content {
    width: 50%;
    margin-left: 50px;
  }
  .services__icon {
    width: 50%;
    margin: 0;
  }
  .services__card-content {
    p {
      padding: 0;
      max-width: 400px;
    }
  }
}
