
/*
 * Product-TABS scss File
*/

@import '../../flex.scss'; 

$bg: #262a33;
$border: #e8e2d6;


.tab-links {
  background-color: #fff;
  overflow: hidden;
}
.tab-link {
  background: transparent;
  border: none;
  outline: none;
  background-color: #fff;
  color: #bdc3c7;
  font-family: 'Lato', sans-serif;
  font-size: 24px;
  font-weight: bold;
  border: 2px solid transparent;
  padding: 20px 50px;
}
.tab-link-active {
  color: #ffffff;
  background-color: $bg;
  border-color: $border;
  border-bottom-color: transparent;
  border-top-left-radius: 18px;
  border-top-right-radius: 18px;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    height: 2px; 
    width: 800px;
    background-color: $border;
    right: calc(100% + 2px);
    top: 100%;
  }
  &::after {
    content: '';
    position: absolute;
    height: 2px; 
    width: 800px;
    background-color: $border;
    left: 100%;
    top: 100%;
  }
}
.tab-content {
  background-color: $bg;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  color: #fff;
  text-align: left;
  p, h6 {
    color: #ffffff;
  }
  p {
    font-size: 16px;
    font-weight: 400;
    &:nth-child(2) {
      margin-top: 23px;
    }
  }
}
.tab-content__inner {
  @include flex;
}
.tab-content__block {
  padding: 60px;
}

