/*
 * Footer scss File
*/

@import '../../flex.scss'; 

.footer {
  &.print {
    display: none;
  }
}
.footer__inner {
  @include flex;
  @include flex-space-between;
  text-align: left;
  padding: 35px 0;
  .subtitle_h6 {
    color: #333333;
    font-size: 13px;
    font-weight: 700;
  }
  .menu-col__list >li >a {
    color: #45413e;
    font-size: 13px;
    opacity: 0.7;
    white-space: nowrap;
  }
}
.footer__menu-wr {
  @include flex;
}
.copyright {
  margin-bottom: 25px;
}
.mail {
  margin: 20px 0;
}
.social-wr {
  white-space: nowrap;
}
.social {
  margin: 0 12px;
  svg {
    width: 20px;
    height: 20px;
  }
  &:hover {
    svg {
      g {
        opacity: 1;
      }
    }
  }
}


.footer__mob-menu {
  width: 580px;
  margin: 0 auto;
  display: none;
}

/* ______________________ [ MEDIA ] __________________ */


@media (max-width: 960px) {
  .footer__mob-menu {
    display: block;
  }
  .icons-wr {
    &.mobile {
      display: block;
    }
  }
  .menu-col,.subtitle_h6 {
    display: none; 
  }
  .adress, .mail, .phone, .copyright {
    &.desc {
      display: none;
    }
  }
  .footer__inner {
    @include flex-column;
  }
  .social-wr {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 65px;
  }
  .social {
    margin: 0 16px;
  }
}
@media (max-width: 768px) { 

}
@media (max-width: 600px) {
.footer__mob-menu {
    width: 450px;
  }
  .print__inner {
    .title_h2 {
      font-size: 26px;
    }
  }
}
@media (max-width: 480px) {
.footer__mob-menu {
    width: 350px;
  }
}
@media (max-width: 360px) {
.footer__mob-menu {
    width: 300px;
  }
  .footer {
    .content-wr {
      padding-bottom: 30px;
    }
  }
}



