
/*
 * Release scss File
*/

@import '../../flex.scss'; 

$bg: #f1f1f1;
$textColor: #45413e;


.release {
  .content-wr {
    padding-bottom: 50px;
    padding-top: 140px;
    background-color: $bg;
  }
}
.release-inner {
  background-color: #fff;
  border-radius: 6px;
  padding: 60px;
  >p {
    color: $textColor;
    margin-bottom: 100px;
  }
}
.release-title {
  color: $textColor;
  font-size: 30px;
  font-weight: 900;
  margin-bottom: 10px;
}
.counter-wrapper {
  @include flex-vh-center;
  h2 {
    color: $textColor;
    font-size: 48px;
    font-weight: 900;
  }
  h3 {
    position: absolute;
    top: 100%;
    color: $textColor;
    font-size: 18px;
    margin-top: 20px;
  }
}
.seconds-progress,
.minutes-progress,
.hours-progress,
.days-progress,
.months-progress {
  position: relative;
  height: 110px;
  width: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
}
.seconds-svg,
.minutes-svg,
.hours-svg,
.days-svg,
.months-svg {
  position: absolute;
  top: calc(50% - 60px);
  left: calc(50% - 60px);
  transform: rotate(-90deg);
}

.announcements-wr {
  @include flex-vh-center;
  width: 480px;
  height: 36px;
  margin: 0 auto;
  margin-top: 100px;
  border: 2px solid #e8e2d6;
  border-radius: 40px;
  position: relative;
  box-sizing: border-box;
}
.announcements {
  height: 100%;
  width: 100%;
  padding-left: 20px;
  padding-right: 40px;
  color: $textColor;
  border: none;
  box-sizing: border-box;
  outline: none;
  background: transparent;
  &:focus { outline: none; }
  &::placeholder {
    color: $textColor;
    font-family: 'Lato', sans-serif;
  }
}
.announcements-btn {
  width: 23px;
  height: 23px;
  border: none;
  box-sizing: border-box;
  outline: none;
  &:focus { outline: none; }
  background: transparent;
  background-image: url('/icons/release-arrow.png');
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #84bc22;
  border-radius: 100%;
  position: absolute;
  right: 11px;
  top: 4px;
}

/* ______________________ [ MEDIA ] __________________ */

@media (max-width: 800px) {
  .release-inner {
    padding: 60px 10px;
  }
}
@media (max-width: 700px) {
  .counter-wrapper {
    @include flex-column;
  }
  .seconds-progress,
  .minutes-progress,
  .hours-progress,
  .days-progress,
  .months-progress {
    margin: 30px 0;
  }
  .release-inner > p {
    margin-bottom: 40px;
  }
  .announcements-wr {
    margin-top: 80px;
  }
  .announcements-wr {
    width: 80%;
  }
}
